.tabs-loan--2 {


	

	@media (max-width: 767.98px) {
		.tabs-loan__content {
			display: none !important;
		}
		.tabs-loan__content[data-id='#personales'] {
			display: block !important;
		}
	}

	.tabs-loan__wrapper {
		position: relative;
		margin-left: 150px;
		margin-right: 90px;
		border-radius: 10px;

		@media (max-width: 1199.98px) {
			margin-left: 0;
			margin-right: 0;
		}

		&::before {
			position: absolute;
			content: '';
			width: 100%;
			height: 84px;
			box-shadow: 0px 0px 60px rgba(15, 35, 52, 0.35);
			z-index: -1;
		}
	}
}

